import React from "react";
import { NavLink } from "react-router-dom";
import "./Navigation.css";
import "./Navigation_text.css";

const Navigation = () => (
    <nav className="navbar">


        <NavLink 
        exact
        activeClassName="navbar__link--active"
        className="nav_text_container navbar__link"
        to="/">
            <svg viewBox="0 0 210 60">
                <g stroke="none" fill="none" fill-rule="evenodd" fill-opacity="0">
                    <text stroke="#2E294E" fill="#645F5A" font-family="PermanentMarker-Regular, Permanent Marker" font-size="40">
                        <tspan x="10" y="50">
                            <tspan>@HOME</tspan>
                        </tspan>
                    </text>
                </g>
            </svg>
        </NavLink>
        <NavLink       
            activeClassName="navbar__link--active"
            className="nav_text_container navbar__link" 
            to="/about">
                <svg viewBox="0 0 220 60">
                    <g stroke="none" fill="none" fill-rule="evenodd" fill-opacity="0">
                        <text stroke="#2E294E" fill="#645F5A" font-family="PermanentMarker-Regular, Permanent Marker" font-size="40">
                            <tspan x="10" y="50">
                                <tspan>@About</tspan>
                            </tspan>
                        </text>
                    </g>
                </svg>
        </NavLink>
    </nav>
)

export default Navigation;